import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = [];

/*
    Example: <srs-card status="error" content="Please contact system administrator." prefix="error"/>
*/
const SrsCard = function (props, ref) {

    function handleClick() {
        props.dispatchEvent(EventAPI.CARD_CLICKED, { payload: {id: props.elementid}});
    }

    return (
        <div onClick={handleClick}>
            {props.title ? (<h2 className="bx--card__title">{props.title}</h2>) : <slot name="cardheader" />}
            <div className="bx--card__body">
                <slot name="cardbody" />
            </div>
        </div>
    );
}

const styles = `
    
`
const props = [{
    name: "title",
    defaultValue: "",
    type: "string"
}]

const slots = [{name: "cardbody"}];

const WrappedComponent = wrapper(React.forwardRef(SrsCard), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };